:root {
  --purple: #fd58e0;
  --purple-blue: #b664fa;
  --blue: #5958fd;
  --glass-color1: rgba(240, 197, 249, 0.6);
  --glass-color2: rgba(197, 210, 249, 0.3);
  --glass-color3: rgba(255, 255, 255, 0.15);
  --shadow-color1: rgba(0, 0, 0, 0.4);
  --shadow-color2: rgba(255, 255, 255, 0.7);
}

.app {
  height: 100vh;
  padding-left: 38.5%;
  background: -webkit-gradient(
    linear,
    right top, left top,
    from(var(--purple)),
    color-stop(20%, var(--purple)),
    color-stop(20%, var(--purple-blue)),
    color-stop(23%, var(--purple-blue)),
    color-stop(23%, var(--blue))
  );
  background: -webkit-linear-gradient(
    right,
    var(--purple),
    var(--purple) 20%,
    var(--purple-blue) 20%,
    var(--purple-blue) 23%,
    var(--blue) 23%
  );
  background: -o-linear-gradient(
    right,
    var(--purple),
    var(--purple) 20%,
    var(--purple-blue) 20%,
    var(--purple-blue) 23%,
    var(--blue) 23%
  );
  background: linear-gradient(
    to left,
    var(--purple),
    var(--purple) 20%,
    var(--purple-blue) 20%,
    var(--purple-blue) 23%,
    var(--blue) 23%
  );

  /* Flex Box */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
}

.app * {
  font-family: "Rubik", sans-serif;
  font-size: 12pt;
  font-weight: 400;
}

/* -----------
    Styling form
    ------------- */

.sign-in-form {
  width: 500px;
  height: 500px;
  border-radius: 30px;
  background: -webkit-gradient(
    linear,
    left top, right bottom,
    from(var(--glass-color1)),
    to(var(--glass-color2))
  );
  background: -webkit-linear-gradient(
    top left,
    var(--glass-color1),
    var(--glass-color2)
  );
  background: -o-linear-gradient(
    top left,
    var(--glass-color1),
    var(--glass-color2)
  );
  background: linear-gradient(
    to bottom right,
    var(--glass-color1),
    var(--glass-color2)
  );
  position: relative;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  -webkit-filter: blur(50px);
          filter: blur(50px);
  -webkit-animation: show-form 2s cubic-bezier(0.4, 0, 0.2, 1) forwards;
          animation: show-form 2s cubic-bezier(0.4, 0, 0.2, 1) forwards;
  z-index: 2;

  /* Grid */
  display: grid;
  grid-template-columns: 60%;
  grid-template-rows: 75px 1fr 1fr auto 1fr 1fr 70px 50px;
  -ms-flex-pack: center;
      justify-content: center;
  grid-template-areas:
    "."
    "username"
    "password"
    "show"
    "."
    "."
    "submit"
    "new";
}

.sign-in-form input[type="text"],
.sign-in-form input[type="password"] {
  height: 50px;
  padding-left: 15px;
  border-radius: 25px;
  border: none;
  background-color: var(--glass-color3);
  -webkit-box-shadow: 1px 1px 10px var(--shadow-color1) inset,
    -2px -2px 10px var(--shadow-color2) inset;
          box-shadow: 1px 1px 10px var(--shadow-color1) inset,
    -2px -2px 10px var(--shadow-color2) inset;
}

.sign-in-form input[type="text"]:focus::-webkit-input-placeholder,
.sign-in-form input[type="password"]:focus::-webkit-input-placeholder {
  color: var(--purple-blue);
}

.sign-in-form input[type="text"]:focus::-moz-placeholder,
.sign-in-form input[type="password"]:focus::-moz-placeholder {
  color: var(--purple-blue);
}

.sign-in-form input[type="text"]:focus::-ms-input-placeholder,
.sign-in-form input[type="password"]:focus::-ms-input-placeholder {
  color: var(--purple-blue);
}

.sign-in-form input[type="text"]:focus::placeholder,
.sign-in-form input[type="password"]:focus::placeholder {
  color: var(--purple-blue);
}

.sign-in-form input[type="text"] {
  /* Grid */
  grid-area: username;
}

.sign-in-form input[type="password"] {
  /* Grid */
  grid-area: password;
}

.show-pass {
  /* Grid */
  grid-area: show;
}

.show-pass span {
  position: relative;
  top: -3px;
}

.checkbox {
  display: inline-block;
  margin: 0 10px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: var(--glass-color3);
  -webkit-box-shadow: 1px 1px 5px var(--shadow-color1) inset,
    -2px -2px 5px var(--shadow-color2) inset;
          box-shadow: 1px 1px 5px var(--shadow-color1) inset,
    -2px -2px 5px var(--shadow-color2) inset;
}

.checked {
  background: -webkit-gradient(
    linear,
    left top, right bottom,
    from(var(--purple)),
    to(var(--purple-blue))
  );
  background: -webkit-linear-gradient(
    top left,
    var(--purple),
    var(--purple-blue)
  );
  background: -o-linear-gradient(
    top left,
    var(--purple),
    var(--purple-blue)
  );
  background: linear-gradient(
    to bottom right,
    var(--purple),
    var(--purple-blue)
  );
  -webkit-box-shadow: -1px -1px 5px var(--shadow-color1) inset,
    2px 2px 5px var(--shadow-color2) inset;
          box-shadow: -1px -1px 5px var(--shadow-color1) inset,
    2px 2px 5px var(--shadow-color2) inset;
}

.sign-in-form input[type="submit"] {
  width: 120px;
  height: 50px;
  border: none;
  margin: 0 auto;
  font-size: 16pt;
  color: rgba(0, 0, 0, 0.9);
  cursor: pointer;
  background: -webkit-gradient(
    linear,
    left top, right bottom,
    from(var(--purple)),
    to(var(--purple-blue))
  );
  background: -webkit-linear-gradient(
    top left,
    var(--purple),
    var(--purple-blue)
  );
  background: -o-linear-gradient(
    top left,
    var(--purple),
    var(--purple-blue)
  );
  background: linear-gradient(
    to bottom right,
    var(--purple),
    var(--purple-blue)
  );
  opacity: 0.8;
  border-radius: 25px;
  -webkit-box-shadow: 2px 2px 10px var(--shadow-color2) inset,
    -1px -1px 10px var(--shadow-color1) inset;
          box-shadow: 2px 2px 10px var(--shadow-color2) inset,
    -1px -1px 10px var(--shadow-color1) inset;
  -webkit-transition: -webkit-box-shadow 0.05s ease;
  transition: -webkit-box-shadow 0.05s ease;
  -o-transition: box-shadow 0.05s ease;
  transition: box-shadow 0.05s ease;
  transition: box-shadow 0.05s ease, -webkit-box-shadow 0.05s ease;

  /* Grid */
  grid-area: submit;
}

.sign-in-form input[type="submit"]:active {
  -webkit-box-shadow: 1px 1px 10px var(--shadow-color1) inset,
    -2px -2px 10px var(--shadow-color2) inset;
          box-shadow: 1px 1px 10px var(--shadow-color1) inset,
    -2px -2px 10px var(--shadow-color2) inset;
}

.have-account {
  text-align: center;

  /* Grid */
  grid-area: new;
}

.have-account a:link,
.have-account a:visited {
  color: blue;
  text-decoration: none;
  -webkit-transition: color 0.1s ease;
  -o-transition: color 0.1s ease;
  transition: color 0.1s ease;
}

.have-account a:hover,
.have-account a:active {
  color: var(--blue);
}

/* -----------
    Styling bubbles
    ----------------*/

.bubble {
  display: block;
  position: absolute;
  border-radius: 50%;
  background: -webkit-gradient(
    linear,
    left top, right bottom,
    from(var(--purple)),
    to(var(--purple-blue))
  );
  background: -webkit-linear-gradient(
    top left,
    var(--purple),
    var(--purple-blue)
  );
  background: -o-linear-gradient(
    top left,
    var(--purple),
    var(--purple-blue)
  );
  background: linear-gradient(
    to bottom right,
    var(--purple),
    var(--purple-blue)
  );
  -webkit-box-shadow: 4px 4px 15px var(--shadow-color2) inset,
    -4px -4px 15px var(--shadow-color1) inset;
          box-shadow: 4px 4px 15px var(--shadow-color2) inset,
    -4px -4px 15px var(--shadow-color1) inset;
}

.bubble1 {
  top: 10%;
  left: 5%;
  width: 50px;
  height: 50px;
}

.bubble2 {
  top: 40%;
  left: 15%;
  width: 100px;
  height: 100px;
}

.bubble3 {
  top: 17%;
  left: 65%;
  width: 70px;
  height: 70px;
}

.bubble4 {
  top: 70%;
  left: 53%;
  width: 150px;
  height: 150px;
}

/* ----------
    ANIMATIONS
    ------------ */

@-webkit-keyframes show-form {
  100% {
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-backdrop-filter: blur(3px);
            backdrop-filter: blur(3px);
  }
}

@keyframes show-form {
  100% {
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-backdrop-filter: blur(3px);
            backdrop-filter: blur(3px);
  }
}



